@import url(https://rsms.me/inter/inter.css);
:root {
  --grey1: #7e7e7e;
  --grey2: #A6A6A6;
  --grey3: #E5E5E5;
  --lightgrey: #f2f1f7;
  --green: #18D873;
  --orange: #E06417;
}

body {
  margin: 0;
  font-family: Inter, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-title {
  display: inline;
  font-weight: bold;
  font-size: 5rem;
  background: linear-gradient(90deg, #4c37a1, #0097ec);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

.language {
  color: var(--grey1);
}

.language:hover {
  color: var(--grey1);
  text-decoration: underline;
}

@media screen and (max-width: 768px) {
  .header-title {
    font-size: 3rem;
  }
}

@media screen and (max-width: 576px) {
  .header-title {
    font-size: 2rem;
  }
}

.box {
  height: 300px;
  width: 100%;
  border-radius: 1em;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  color: #fff;
  overflow: hidden;
}

.box.box-eatuntil {
  background-color: #18d873;
}

.box.box-bql {
  background-color: #e06417;
}

.box-title {
  font-size: 2.1rem;
  font-weight: bold;
}

.box-text {
  font-size: 1.2rem;
}

.box-logo {
  height: 5em;
  margin-top: auto;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
  margin-bottom: 1rem;
}

.box-screen {
  background-position: top;
  background-size: cover;
  background-repeat: no-repeat;
}

.box-screen-eu {
  background-image: url(/static/media/eu-screen-light.679d8ca2.png);
}

.box-screen-bql {
  background-image: url(/static/media/bql-screen-light.8b18bfe6.png);
}

.soon {
  color: var(--grey1);
  margin: 0;
  margin-right: 0.5em;
}

.store-badge {
  height: 40px;
}

@media screen and (max-width: 768px) {
  .box-title {
    font-size: 1.7rem;
  }

  .box-text {
    font-size: 1rem;
  }

  .store-badge {
    height: 30px;
  }
}

footer p {
  color: var(--grey2);
  font-size: 0.9rem;
}

footer a {
  color: var(--grey2);
  text-decoration: underline;
  margin: 0 1em;
}

footer a:hover {
  color: var(--grey1);
}

.wrapper {
  height: 100vh;
}

